import { h } from 'preact';
import PropTypes from 'prop-types';
import Icon from './../Icon/index.jsx';


export default function Modal({ children, title, closeAction }) {
  return (
    <div className="Modal">
      <div className="Modal-overlay" onClick={closeAction} aria-hidden />
      <div claclassNamess="Modal-boxWrap">
        <div claclassNamess="Modal-box">
          <div className="Modal-heading">{ title }</div>
          <div className="Modal-content">{ children }</div>
        </div>
        <button className="Modal-close" onClick={closeAction}>
          <Icon type="close" />
        </button>
      </div>
    </div>
  );
}


Modal.propTypes = {
  children: PropTypes.node.isRequired,
  title: PropTypes.string.isRequired,
  closeAction: PropTypes.func.isRequired,
};




